* {
  box-sizing: border-box;
}

body {
  background: linear-gradient(45deg, black, gray, black);
}

.drum-machine {
  width: 700px;
  height: 400px;
  border: 4px solid rgb(106, 24, 24);
  margin: 190px auto;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  background: linear-gradient(
    217deg, 
    rgba(78, 15, 15, 0.8), 
    rgba(255,0,0,0) 70.71%),            
    linear-gradient(127deg, rgba(18, 181, 18, 0.8), 
    rgba(55, 60, 55, 0) 70.71%),            
    linear-gradient(336deg, rgba(54, 54, 166, 0.8), 
    rgba(0,0,255,0) 70.71%);
}

.keypad {
  width: 360px;
  height: 350px;
  /* background-color: orange; */
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;
}

.side-panel-container {
  width: 300px;
  height: 350px;
  /* background-color: cornflowerblue; */
}

.sidePanel {
  width: 150px;
  height: 200px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;
  margin-right: auto;
  margin-left: auto;
  margin-top: 50px;

}

.drum-pad {
  width: 100px;
  height: 100px;
  margin: auto;
  margin-top: 2.5px;
  box-shadow: 3px 3px black;
  border-radius: 50%;
  background: radial-gradient(rgb(183, 182, 182), rgb(36, 36, 36));
  font-size: 30px;
  text-align: center;
  padding-top: 30px;
  color:rgb(99, 15, 15);
  cursor: default;
}

.padHolder {
  width: 105px;
  height: 105px;
  background-color: aqua;
  border-radius: 50%;
}

button { 
  background-color: rgb(8, 63, 13);
  width: 100px;
  height: 30px;
  display: block;
  border-radius: 30px;
  border: 3px solid rgb(31, 31, 31);
  margin-top: -10px;
  cursor: pointer;
}

h1 {
  width: 100%;
  color: rgb(19, 18, 18);
  text-align: center;
  font-size: 40px;
  font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
}

#currentSound {
  width: 300px;
  height: 50px;
  border: 3px solid orange;
  border-radius: 30px;
  text-align: center;
  font-weight: bold;
  font-size: 15px;
  background-color: #504e4e;
  margin-top: -30px;
  cursor: default;
}

#creator {
  text-align: center;
  font-style: italic;
  font-size: 20px;
}

#volumeButton {
  margin-top: -10px;
}

.speaker {
  width: 70px;
  height: 70px;
  border-radius: 50%;
  background-color: rgb(34, 29, 29);
}

.subSpeaker {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  margin: auto;
  margin-top: 10px;
  background-color: rgb(61, 52, 52);
}

#volumeButton {
  cursor: pointer;
}

